const chartRef = ref()
const drawSg =()=>{// D3js
        let chart = d3.select(chartRef.value)
        let z = d3.scaleOrdinal().range(d3.schemeCategory20)
        let g = chart.append('g').attr('transform', 'translate(200,80)')//translate(150,100)
        let bp = viz
                .bP() // BP
                .data(data)
                .min(12)
                .pad(1)
                .height(700)
                .width(400)
                .barSize(30)
                .fill(d => z(d.primary));
        // BP
        g.call(bp) // BP
        g.append('text')
                .attr('x', -50)
                .attr('y', -8)
                .style('text-anchor', 'middle')
                .text('')

        g.append('text')
                .attr('x', 430)
                .attr('y', -8)
                .style('text-anchor', 'middle')
                .text('')

        g.selectAll('.mainBars')//
                .on('mouseover', mouseover)
                .on('mouseout', mouseout)

        g.selectAll('.mainBars')
                .append('text')
                .attr('class', 'label')
                .attr('x', d => (d.part === 'primary' ? -30 : 30))
                .attr('y', d => +6)
                .text(d => d.key)
                .attr('text-anchor', d => (d.part === 'primary' ? 'end' : 'start'))

        g.selectAll('.mainBars')
                .append('text')
                .attr('class', 'perc')
                .attr('x', d => (d.part === 'primary' ? -100 : 80))
                .attr('y', d => +6)
                .text(function(d) {
                  return d3.format('0.0%')(d.percent)
                })
                .attr('text-anchor', d => (d.part === 'primary' ? 'end' : 'start'))

        g.selectAll('.mainBars') // hover
                .append('title')
                .text(function(d) {
                  return d.key + '\n' + d.value + ' '
                })

        function mouseover(d) {
          bp.mouseover(d)

          g.selectAll('.mainBars')
                  .select('.perc')
                  .text(function(d) {
                    return d3.format('0.0%')(d.percent)
                  })

          g.selectAll('.mainBars')
                  .select('title')
                  .text(function(d) {
                    return d.key + '\n' + d.value + ' '
                  })
        }
        function mouseout(d) {
          bp.mouseout(d)
          g.selectAll('.mainBars')
                  .select('.perc')
                  .text(function(d) {
                    return d3.format('0.0%')(d.percent)
                  })

          g.selectAll('.mainBars')
                  .select('title')
                  .text(function(d) {
